.App {
  background-color: var(--background-dark);
  min-height: 100vh;
}

* {
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  color: var(--primary);
  margin: 0;
  padding: .5em 0;
  text-align: center;
  width: 100vw;
  /* flex-shrink: 0; */
}

h1 {
  font-size: 65px;
}

@keyframes slideFromBottom {
  0% {
    transform: translateY(60vh);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.Header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.language-selection {
  color: var(--primary);
  font-size: 1.25em;
  transform: translateY(-1em);
  cursor: pointer;
  user-select: none;
}

.flag {
  height: .8em;
  margin-right: .25em;
}

@media only screen and (min-width: 650px) {
  /*dektop header*/
  .Header {
    flex-direction: row;
  }
  
  .language-selection{
    position: absolute;
    margin: 0;
    transform: translateX(2em);
  }
}

.projects {
  color: var(--primary);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: center;
  /* overflow: hidden; */
}

hr {
  border-color: var(--primary);
  margin-top: 0;
  margin-bottom: 2em;
}

.project {
  background-color: var(--background);
  border: .2em solid var(--primary-dark);
  border-radius: .7em;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 2em;
  padding: .5em;
  max-width: min(90%, 20em);
  opacity: 0;
  transition: 0.5s ease-out;
}

.project-visible {
  /* animation: slideFromBottom ease 0.35s; */
  animation: slideFromBottom var(--popup-bezier) 0.5s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  opacity: 1;
}

.project:hover {
  background-color: var(--background-light);
}

.project-title {
  font-weight: 700;
  font-size: 1.75em;
}

.project-description {
  margin-top: .5em;
  margin-bottom: .75em;
}

.project-button {
  border: .15em solid var(--primary);
  border-radius: .5em;
  padding: .2em;
  margin-bottom: .5em;
  cursor: pointer;
  user-select: none;
}

.project-button:hover {
  color: var(--background);
  background-color: var(--primary);
  font-weight: 600;
  transition: 0.4s ease-out;
}

.project-button:active {
  color: var(--background);
  border-color: var(--primary-dark);
  background-color: var(--primary-dark);
  font-weight: 600;
  transition: none;
}

.hidden {
  opacity: 0;
}

a {
  color: inherit;
  text-decoration: inherit;
}